
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.main
{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Poppins;
    flex-direction: column;
    padding: 30px;
}

.grid-main
{
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 20px;
}

.grid-child
{
    border: 1px solid rgb(201, 197, 197);
    padding: 20px;
    border-radius: 10px;
}

.news-image
{
    width: 100%;
}

img
{
    max-height: 200px;
}

.news-title
{
    font-size: 20px;
    font-weight: 700;
}

.news-content
{
    font-size: 13.5px;
}

.news-author
{
    font-size: 13px;
    font-weight: bold;
}

.news-date
{
    font-size: 13px;
    font-weight: bold;
}

.space-in-between
{
    display: flex;
    justify-content: space-between;
}

.select
{
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 450px;
    font-weight: bold;
}

.select-box
{
    width: 200px;
    height: 40px;
    border: 1px solid rgb(201, 197, 197);
    outline: none;
}