
.pageContainer
{
display: flex;
justify-content: space-between;
flex-wrap: wrap;
}

.pagDetails
{
    width: 100%;
    object-fit: contain;
}

.pagDetails img{
    width: 800px;
    max-height: 60vh;
    padding: 20px 0px 0px 80px;
    opacity: 1;
   }

.pagDetails p
{
    color: black;
    font-size:25px;
    font-weight: 1000;
    font-family:monospace;
    text-decoration: none;
    padding: 0px 0px 0px 80px;
}

.pagDetails2 img
{
    width: 500px;
    max-height: 72vh;
    padding: 20px 200px 0px 0px;
    opacity: 0.9;
}


span
{
    color: rgb(34, 34, 133);
    font-weight: bold;
}


@media screen and (max-width:1200px) {
    .pagDetails{
        width: 100%;
    }

    .pagDetails p
    {
        padding: 93px 0px 0px 10px;
        font-size: 15px;
    }
}